<template>
  <div class="login">
    <div class="container">
    <section id="content">
        <form action="" @submit.prevent="login"> <!-- go to login method on submit -->
            <h1>Login Form</h1>
             <div>{{msg}}</div>
            <div>
                <input type="text" name="email" placeholder="email" v-model="email"  required  id="username" />
            </div>
            <div>
                <input type="password" name="password" placeholder="Password" v-model="password" required id="password" />
            </div>
            <div class="select">
                <select v-model="selected">
                  <option v-for="option in options" v-bind:key="option" :value="option.id">{{ option.name }}</option>
            </select>
            </div>
            <div>
                <input type="submit" value="Log in" />
                <router-link to="/register">Register</router-link>
            </div>
        </form><!-- form to get email and password from user --> 
        
    </section><!-- content -->
</div><!-- container -->
  </div>
</template>

<script>
export default {
  name: 'login',


  data () {
    return {
    //variables to be used in this page
    msg:'',
    selected:'',
    email:'',
    password:'',
    options: [
      {id: "", name: "Account Type"},
      {id: "Provider", name: "Provider"},
      {id: "Teacher", name: "Teacher"},
      {id: "Student", name: "Student"}
    ] //items for the select drop down menu
    }
  },
   methods: {
      login () {
        //fetch request to login route in rest api
         fetch("http://localhost:3000/auth/login", {
        headers: {  "Content-Type": "application/json; charset=utf-8" },
        method: 'post',
        body: JSON.stringify({
              email: this.email,
              password: this.password,
              type: this.selected,
          }) //sending this body as request
        }).then(res => res.json()) //checking response
          .then(jsonData => {
            if(jsonData.success == true){
              if(jsonData.type === "Student" || jsonData.type === "Teacher"){
              this.$router.push({
                name: 'dashboard', //go to student and teacher route if type is student or teacher beacse both can just view
                  })
                }else{
                  this.$router.push({
                      name: 'providerDash', //go to provider route
                  }) 
                }
            }else {
              this.msg = jsonData.msg
            }
          
        })
        .catch((err)=>{
            console.log(err)
        }) 
    }
  }
}
</script>

 <style src='../assets/css/style.css'></style> <!-- loading css file-->

