<template>
  <div class="dashboard">
    <div class="container">
        <h1 style="text-align: center">Welcome Students and Teachers</h1>
        <br/>
        <table class="zui-table">
        <thead>
            <tr>
              <th>Topic</th>
              <th>Location</th>
              <th>Price</th>
              <th>Ranking</th>
              <th>Provider</th>
              <th>author</th>
            </tr>
        </thead>
        <tbody>
            <tr>
                <tr v-for='course in dbTable' v-bind:key="course" class='course'>
                <td >{{course.topic}}</td>
                <td >{{course.location}}</td>
                <td >{{course.price}}</td>
                <td >{{course.rating}}</td>
                <td >{{course.provider}}</td>
                <td >{{course.author}}</td>   
            </tr> 
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'dashboard',


  data () {
    return {
      dbTable:[]
    }
  },
   methods: {

    },
    created() {
    //fetching all courses from database on page load
    fetch("http://localhost:3000/api/getcourses", {
    method:'GET',
    headers: {  "Content-Type": "application/json; charset=utf-8" },
    }).then(res => res.json())
    .then(jsonData => {
            jsonData.forEach(element => {
                let newItem = {
                id: element._id,
                topic: element.topic,
                location: element.location,
                price: element.price,
                rating: element.rating,
                provider:element.provider,
                author:element.author
            };

            this.dbTable.push(newItem);
            });
    })
        .catch((err) => {
        console.log(err)
    })
    }
  }

</script>


