<template>
  <div class="addr">
        <div class="container">
        <section id="content">
            <form action="" @submit.prevent="addCourse">
              <div>{{msg}}</div>
                <h1>Add Review</h1>
                <div>
                    <input type="text" id="topic" placeholder="topic" v-model='topic' required />
                </div>
                <div>
                    <input type="text" id="rating" placeholder="rating" v-model='rating'/>
                </div>
                <div>
                    <input type="text" id="review" placeholder="review" v-model='review'/>
                </div>
                <div>
                    <input type="text" id="author" placeholder="review" v-model='author'/>
                </div>
                <div>
                    <input type="submit" value="Add Course" />
                    <router-link to="/">Login</router-link>
                </div>
            </form><!-- form -->
            
        </section><!-- content -->
    </div><!-- container -->
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'login',


  data () {
    return {
    topic:'',
    rating:'',
    review:'',
    author:'',
    }
  },
   methods: {
      addCourse () {
          //sending post requeest to add review route in rest api using fetch
          fetch("http://localhost:3000/api/rankings", {
            headers: {  "Content-Type": "application/json; charset=utf-8" },
            method: 'post',
            body: JSON.stringify({
                    topic: this.topic,
                    rating: this.rating,
                    review: this.review,
                    author:this.author
                })
            }).then(res => res.json())
          .then(jsonData => {
            if(jsonData.success == true){
                this.$router.push({
                      name: 'providerDash',
                  })
                }else{
                  this.msg = jsonData.msg
                }
        })
        .catch((err)=>{
            console.log(err)
        })
    }
  }
}
</script>


