<template>
  <div class="updater">
    <div class="container">
        <section id="content">
            <form action="" @submit.prevent="update">
              <div>{{msg}}</div>
                <h1>Update Review</h1>
                <div>
                    <input type="text" id="topic" :placeholder="[[topic]]" v-model='topic' required />
                </div>
                <div>
                    <input type="text" id="rating" :placeholder="[[rating]]" v-model='rating' required/>
                </div>
                <div>
                    <input type="text" id="review" :placeholder="[[review]]" v-model='review' required/>
                </div>
                <div>
                    <input type="text" id="author" :placeholder="[[author]]" v-model='author' required/>
                </div>
                <div>
                    <input type="submit" value="Update Review" />
                </div>
            </form><!-- form -->
            
        </section><!-- content -->
    </div><!-- container -->
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'login',


  data () {
    return {
    id:'',
    topic:'',
    rating:'',
    review:'',
    author:'',
    }
  },
   methods: {

       update () {
        //sending put requeest to update ranking route in rest api using fetch
             fetch('http://localhost:3000/api/rankings/'+ this.id, {
            headers: {  "Content-Type": "application/json; charset=utf-8" },
            method: 'put',
            body: JSON.stringify({
                    topic: this.topic,
                    rating: this.rating,
                    review: this.review,
                    author:this.author
                })
            }).then(res => res.json())
          .then(jsonData => {
            if(jsonData.success == true){
                this.$router.push({
                      name: 'providerDash',
                  })
                }else{
                  this.msg = jsonData.msg
                }
        })
        .catch((err)=>{
          console.log(err)  
       })
       }
  },
  mounted: function() {
    //get params from url
      this.id = this.$route.params.id;
      this.topic = this.$route.params.topic;
      this.rating = this.$route.params.rating;
      this.review = this.$route.params.review;
      this.author = this.$route.params.author;
  }
}
</script>


