import Vue from 'vue';
import VueRouter from 'vue-router';
import login from '../views/login.vue'; //login file
import register from '../views/register.vue'; //register file
import provider from '../views/provider.vue'; //provider file
import dash from '../views/dashboard.vue'; //student and teacher dashboard file
import add from '../views/addC.vue'; //add course from this file
import update from '../views/updateC.vue'; //update course from this file
import addr from '../views/addR.vue'; //add review from this file
import updater from '../views/updateR.vue'; //update review from this file

//loaded all files for routing
Vue.use(VueRouter) //vue route is being used for routing

const routes = [
  {
    path: '/', //path describes the route after localhost:8080/ so this is the default route
    name: 'login', //name of the route , in vue this is used for routing
    component: login // compoment is used to attach a file to the path
  }, // default / route is the login page
  {
    path: '/register',
    name: 'register',
    component:register
  }, // this is hou routes are defined , define path,name and attach the component
  {
    path: '/dashboard',
    name: 'dashboard',
    component:dash
  },
  {
    path: '/providerDash',
    name: 'providerDash',
    component:provider
  },
  {
    path: '/add',
    name: 'add',
    component:add
  },
  {
    path: '/update/:id/:topic/:location/:price/:rating/:provider/:author',
    name: 'update',
    component:update
  }, //this route takes params for updating
  {
    path: '/updater/:id/:topic/:rating/:review/:author',
    name: 'updater',
    component:updater
  },
  {
    path: '/addr',
    name: 'addr',
    component:addr
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
}) // history mode means we dont have to add # with routes

export default router
