/* eslint-disable no-console */

import { register } from 'register-service-worker'

//this file registers the service worker by including service-worker.js file in the dist directory

if (process.env.NODE_ENV === 'production') {
  register(`${process.env.BASE_URL}service-worker.js`, {
    ready () { //reads server-worker and gets it ready
      console.log(
        'App is being served from cache by a service worker.\n' +
        'For more details, visit https://goo.gl/AFskqB'
      )
    },
    registered () { //once ready it is registered
      //we can now see the service worker if we go to inspect in browser and go to application
      console.log('Service worker has been registered.')
    },
    cached () {
      console.log('Content has been cached for offline use.')
    },
    updatefound () {
      console.log('New content is downloading.')
    },
    updated () {
      console.log('New content is available; Refresh...')
      setTimeout(() => {
        window.location.reload(true)
      }, 1000000000) // when items are upated in service worker we can refresh the app
      //this timer can be decreased as wanted
    },
    offline () {
      console.log('No internet connection found. App is running in offline mode.')
    },
    error (error) {
      console.error('Error during service worker registration:', error)
    }
  })
}
