<template>
  <div class="add">
    <div class="container">
        <section id="content">
            <form action="" @submit.prevent="update">
              <div>{{msg}}</div>
                <h1>Update Course</h1>
                <div>
                    <input type="text" id="topic" :placeholder="[[topic]]" v-model='topic' required />
                </div>
                <div>
                    <input type="text" id="location" :placeholder="[[location]]" v-model='location'/>
                </div>
                <div>
                    <input type="text" id="price" :placeholder="[[price]]" v-model='price'/>
                </div>
                <div>
                    <input type="text" id="rating" :placeholder="[[rating]]" v-model='rating'/>
                </div>
                <div>
                    <input type="text" id="provider" :placeholder="[[provider]]" v-model='provider'/>
                </div>
                <div>
                    <input type="text" id="author" :placeholder="[[author]]" v-model='author'/>
                </div>
                <div>
                    <input type="submit" value="Update Course" />
                </div>
            </form><!-- form -->
            
        </section><!-- content -->
    </div><!-- container -->
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'login',


  data () {
    return {
    id:'',
    topic:'',
    location:'',
    price:'',
    rating:'',
    provider:'',
    author:'',
    newtopic:'',
    newlocation:'',
    newprice:'',
    newrating:'',
    newprovider:'',
    newauthor:'',
    }
  },
   methods: {

       update () {
        //sending put requeest to update course route in rest api using fetch
             fetch('http://localhost:3000/api/courses/'+ this.id, {
            headers: {  "Content-Type": "application/json; charset=utf-8" },
            method: 'put',
            body: JSON.stringify({
                    topic: this.topic,
                    location: this.location,
                    price: this.price,
                    rating: this.rating,
                    provider:this.provider,
                    author:this.author
                })
            }).then(res => res.json())
          .then(jsonData => {
            if(jsonData.success == true){
                this.$router.push({
                      name: 'providerDash',
                  })
                }else{
                  this.msg = jsonData.msg
                }
        })
        .catch((err)=>{
          console.log(err)  
       })
       }
  },
  mounted: function() {

    //get all the params from url
      this.id = this.$route.params.id;
      this.topic = this.$route.params.topic;
      this.location = this.$route.params.location;
      this.price = this.$route.params.price;
      this.rating = this.$route.params.rating;
      this.provider = this.$route.params.provider;
      this.author = this.$route.params.author;
  }
}
</script>


