<template>
  <div class="register">
        <div class="container">
        <section id="content">
            <form action="" @submit.prevent="register">
              <div>{{msg}}</div>
                <h1>Register Form</h1>
                <div>
                    <input type="text" name="email" placeholder="email" v-model="email"  required  id="username" />
                </div>
                <div>
                    <input type="password" name="password" placeholder="Password" v-model="password" required id="password" />
                </div>
                <div class="select">
                <select v-model="selected">
                  <option v-for="option in options" v-bind:key="option" :value="option.id">{{ option.name }}</option>
                  </select>
                </div>
                <div>
                    <input type="submit" value="Sign up" />
                    <router-link to="/">Login</router-link>
                </div>
            </form><!-- form -->
            
        </section><!-- content -->
    </div><!-- container -->

  </div>
</template>

<script>

export default {
  name: 'register',
   data () {
    return {
    selected:'',
    email:'',
    password:'',
    options: [
      {id: "", name: "Account Type"},
      {id: "Provider", name: "Provider"},
      {id: "Teacher", name: "Teacher"},
      {id: "Student", name: "Student"}
    ]
    }
  },
   methods: {
      register () {
        //sending request to rest register route using fetch"
        fetch("http://localhost:3000/auth/register", {
        headers: {  "Content-Type": "application/json; charset=utf-8" },
        method: 'post',
        body: JSON.stringify({
              email: this.email,
              password: this.password,
              type: this.selected,
          })
        }).then(res => res.json())
          .then(jsonData => {
            if(jsonData.success == true){
                this.$router.push({
                      name: 'login',
                  })
                }else{
                  this.msg = jsonData.msg
                }
        })
        .catch((err)=>{
            console.log(err)
        })
        
    }
  }

}
</script>

