<template>
  <div class="providerDash">
    <div class="container">
        <h1 style="text-align: center">Welcome Provider</h1>
        <br/>
        <table class="zui-table"> <!-- defining table to present data -->
        <thead>
            <tr>
              <th>Topic</th>
               <th>Location</th>
               <th>Price</th>
               <th>Ranking</th>
               <th>Provider</th>
               <th>author</th>
               <th>Delete</th>
               <th>Update</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for='course in dbTable' v-bind:key="course" class='course'><!-- looping thourgh the course data recieved from node server and displaying it one by one in table -->
            <td >{{course.topic}}</td>
            <td >{{course.location}}</td>
            <td >{{course.price}}</td>
            <td >{{course.rating}}</td>
            <td >{{course.provider}}</td>
            <td >{{course.author}}</td>
            <td><button v-on:click='deleteItem(course.id)' class="button">Delete</button></td>    
            <td><button v-on:click='Update(course.id,course.topic,course.location,course.price,course.rating,course.provider,course.author)' class="button">Update</button></td>    
            </tr> 
        </tbody>
      </table>
            <div>
            <button class="button" v-on:click='addCourse'>Add Course</button>
        </div>
    </div>
    <br/>
    <br/>
    <div class="container">
        <h1 style="text-align: center">Reviews</h1>
        <br/>
        <table class="zui-table">
        <thead>
            <tr>
               <th>Topic</th>
               <th>Rating</th>
               <th>Review</th>
               <th>author</th>
               <th>update</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for='review in reviewTable' v-bind:key="review" class='review'><!-- looping through reviews data recieved from node server and displaying it one by one is table -->
            <td >{{review.topic}}</td>
            <td >{{review.rating}}</td>
            <td >{{review.review}}</td>
            <td >{{review.author}}</td>
            <td><button v-on:click='UpdateR(review.id,review.topic,review.rating,review.review,review.author)' class="button">Update</button></td>    
            </tr> 
        </tbody>
      </table>
            <div>
            <button class="button" v-on:click='addReview'>Add Review</button>
        </div>
    </div>

   <!-- progressive image loading from cache, cache is already it is already built in service worker -->
   <div class="container">
   <h1 style="text-align: center">Images</h1>
   <br/>
   <h2>English</h2>
   <img src='/img/course/1.jpg' alt='1' width="600" height="600">
   <img src='/img/course/10.jpg' alt='2' width="400" height="400">
   <img src='/img/course/11.jpg' alt='3' width="400" height="400">
   <br/>
   <h2>Science</h2>
   <img src='/img/course/4.png' alt='1' width="400" height="400">
   <img src='/img/course/5.jpg' alt='1' width="400" height="400">
   <img src='/img/course/9.png' alt='1' width="400" height="400">
   <br/>
   <h2>Science</h2>
   <img src='/img/course/6.jpg' alt='1' width="400" height="400">
   <img src='/img/course/7.jpg' alt='1' width="400" height="400">
       </div>


  </div>
</template>

<script>

export default {
  name: 'providerDash',


  data () {
    return {
    dbTable:[],
    reviewTable:[]
    }
  },
   methods: {
       addCourse () {
         //go to add route in vue frontend
           this.$router.push({
                name: 'add',
         })
       },
       addReview () {
         //go to add review route in vue frontend
           this.$router.push({
                name: 'addr',
         })
       },
       deleteItem (id) {
         //sending delete requeest to delete course route in rest api using fetch
             fetch('http://localhost:3000/api/courses/'+ id, {
            headers: {  "Content-Type": "application/json; charset=utf-8" },
            method: 'delete',
            }).then(res => res.json())
          .then(jsonData => {
            if(jsonData.success == true)
               this.$router.push({
                            name: 'providerDash',
                        });
            }).catch((err)=>{
                console.log(err)
            })
       },
       Update(id,topic,location,price,rating,provider,author) {
          //got to update course route in vue rest api
                    this.$router.push({
                            name: 'update',
                            params: {id,topic,location,price,rating,provider,author}
                        });
       },
       UpdateR(id,topic,rating,review,author) {
          //got to update review route in vue rest api
                    this.$router.push({
                            name: 'updater',
                            params: {id,topic,rating,review,author}
                        });
       }
  },
  created() {
    //fetching all courses from database on page load
    fetch("http://localhost:3000/api/getcourses", {
    method:'GET',
    headers: {  "Content-Type": "application/json; charset=utf-8" },
    }).then(res => res.json())
    .then(jsonData => {
            jsonData.forEach(element => {
                let newItem = {
                id: element._id,
                topic: element.topic,
                location: element.location,
                price: element.price,
                rating: element.rating,
                provider:element.provider,
                author:element.author
            }; // creating a new list of items recieved from node server
            this.dbTable.push(newItem); // pushing that list into a new list which can be accessed by the html
            });
         })
        .catch((err) => {
        console.log(err)
    })

    //fetching all Review from database on page load
    fetch("http://localhost:3000/api/rankings", {
    method:'GET',
    headers: {  "Content-Type": "application/json; charset=utf-8" },
    }).then(res => res.json())
    .then(jsonData => {
            jsonData.forEach(element => {
                let newItem = {
                id: element._id,
                topic: element.topic,
                rating: element.rating,
                review: element.review,
                author:element.author
            };// creating a new list of items recieved from node server
            this.reviewTable.push(newItem);
            });// pushing that list into a new list which can be accessed by the html
         })
        .catch((err) => {
        console.log(err)
    })

    }
}
</script>


<style lang="scss">

 //this is just scss lines , sass-loader plugin was insalled for this

$baseColor: #2c3e50;
$borderRadius: 10px;
$imageBig: 400px;
$imageSmall: 400px;
$padding: 10px;

@import url(https://fonts.googleapis.com/css?family=Open+Sans);

body {
   background-color: lighten($baseColor, 30%);
   * { box-sizing: border-box; }
}

.header {
   background-color: darken($baseColor, 5%);
   color: white;
   font-size: 1.5em;
   padding: 1rem;
   text-align: center;
   text-transform: uppercase;
}

img {
   height: $imageSmall;
   width: $imageSmall;
}

.table-users {
   border: 1px solid darken($baseColor, 5%);
   border-radius: $borderRadius;
   box-shadow: 3px 3px 0 rgba(0,0,0,0.1);
   max-width: calc(100% - 2em);
   margin: 1em auto;
   overflow: hidden;
   width: 1400px;
}

table {
   width: 100%;
   
   td, th { 
      color: darken($baseColor, 10%);
      padding: $padding; 
   }
   
   td {
      text-align: center;
      vertical-align: middle;
      
      &:last-child {
         font-size: 0.95em;
         line-height: 1.4;
         text-align: left;
      }
   }
   
   th { 
      background-color: lighten($baseColor, 50%);
      font-weight: 300;
   }
   
   tr {     
      &:nth-child(2n) { background-color: white; }
      &:nth-child(2n+1) { background-color: lighten($baseColor, 55%) }
   }
}

@media screen and (max-width: 700px) {   
   table, tr, td { display: block; }
   
   td {
      &:first-child {
         position: absolute;
         top: 50%;
         transform: translateY(-50%);
         width: $imageBig;
      }

      &:not(:first-child) {
         clear: both;
         margin-left: $imageBig;
         padding: 4px 20px 4px 90px;
         position: relative;
         text-align: left;

         &:before {
            color: lighten($baseColor, 30%);
            content: '';
            display: block;
            left: 0;
            position: absolute;
         }
      }

      &:nth-child(2):before { content: 'Name:'; }
      &:nth-child(3):before { content: 'Email:'; }
      &:nth-child(4):before { content: 'Phone:'; }
      &:nth-child(5):before { content: 'Comments:'; }
   }
   
   tr {
      padding: $padding 0;
      position: relative;
      &:first-child { display: none; }
   }
}

@media screen and (max-width: 500px) {
   .header {
      background-color: transparent;
      color: lighten($baseColor, 60%);
      font-size: 2em;
      font-weight: 700;
      padding: 0;
      text-shadow: 2px 2px 0 rgba(0,0,0,0.1);
   }
   
   img {
      border: 3px solid;
      border-color: lighten($baseColor, 50%);
      height: $imageBig;
      margin: 0.5rem 0;
      width: $imageBig;
   }
   
   td {
      &:first-child { 
         background-color: lighten($baseColor, 45%); 
         border-bottom: 1px solid lighten($baseColor, 30%);
         border-radius: $borderRadius $borderRadius 0 0;
         position: relative;   
         top: 0;
         transform: translateY(0);
         width: 100%;
      }
      
      &:not(:first-child) {
         margin: 0;
         padding: 5px 1em;
         width: 100%;
         
         &:before {
            font-size: .8em;
            padding-top: 0.3em;
            position: relative;
         }
      }
      
      &:last-child  { padding-bottom: 1rem !important; }
   }
   
   tr {
      background-color: white !important;
      border: 1px solid lighten($baseColor, 20%);
      border-radius: $borderRadius;
      box-shadow: 2px 2px 0 rgba(0,0,0,0.1);
      margin: 0.5rem 0;
      padding: 0;
   }
   
   .table-users { 
      border: none; 
      box-shadow: none;
      overflow: visible;
   }
}
 </style>

